/*
  App.css
*/

*{
    -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.title,
.description,
.auth{
  margin: 0;
  padding: 0;
  line-height: 1;
  font-weight: 100;

    pointer-events: none;
}

.title,
.description{
  padding-left: 32px;
}

.title{
  margin-top: 32px;
  font-size: 2.5em;
}

.description{
  margin-top: 10px;
  font-size: 0.5em;
  width: calc( 100vw - 100px );
  font-weight: 400;
  line-height: 1.5;
}

.App article{
  margin: 32px 0;
  padding: 0 32px;
}

.App .mainNav{
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.App .mainNav li{
  display: block;
  float: left;
  margin: 0;
  padding: 0;
  overflow: hidden;

  margin: 0 0.5vw 0.5vw 0;
  width: calc( ( 100vw - 64px - 1vw ) / 3);
  height: 8vw;
  background: #F0F0F0;
  text-align: center;
}

.App .mainNav li:nth-of-type(3n){ margin-right: 0; }

.App .mainNav li a{
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: #333;
}

.App ol{
  margin-left: 0;
  padding-left: 0;
  font-size: 1em;
}

.App ol li{
  list-style-position:inside;
  border-bottom: 1px solid rgba(0,0,0,0.05);
}

.App ol li a{
  display: inline-block;
  width: 4em;
  margin-right: 8px;
  text-decoration: none;
}

.App ol li a:hover{
  text-decoration: underline;
}

.App ol li span{
  margin-right: 8px;
  font-size: 0.5em;
  color: #999;
}

.App footer{
  margin: 32px 0;
  padding: 0 32px;
}

article:after {
  content: "";
  display: block;
  clear: both;
} 
article:before {
  content: "";
  display: block;
  clear: both;
} 
article {
  display: block;
}

footer{
  font-size: 0.5em;
}



.incomplete,
.scrapbox,
.uncategolize,
.ai,
.project
{
  padding: 0 8px;
  border-radius: 16px;
}

.incomplete{
  background: #F66;
  color: #FFF !important;
}

.scrapbox{
  background: #CFC;
}

.uncategolize{
  background: #FFC;
}

.ai{
  background: #CCF;
}

.project{
  background: #F0F0F0;
}


@media screen and (max-width: 375px) {

  .App article{
    padding: 0;
  }

  .App .title{
    font-size: 1.5em;
  }


  .App .mainNav li{
    overflow: hidden;
    display: block;
    float: none;
    margin: 2vw 0 0 0;
    padding: 0;

    width: 100%;
    height: 32vw;
  }

}